import React from "react";
import styled from "styled-components";
import techpic from "../img/techfair2.jpg";

import { Link } from "react-router-dom";
import { Layout, Description, ImageContainer, Hide } from "../styles copy";
// Animation
import { motion } from "framer-motion";
import { titleAnimation, fade, photoAnimation } from "../animation";
import Wave from "./Wave";

const Header = styled(motion.div)`
  @media only screen and (min-width: 540px) and (max-width: 820px) {
    h2 {
      color:black;
      font-size: 3rem;
    }
    h4 {
      color:black;
      font-size: 3rem;
    }
  }
`;

const AboutSection = () => {
  // Framer-motion variant
  const container = {
    hidden: { x: 100 },
    show: {
      x: 0,
      transition: {
        duration: 0.75,
        ease: "easeOut",
        staggerChildren: 0.8,
        when: "afterChildren",
      },
    },
  };

  return (
    <Layout>
      <Description style={{ zIndex: 2 }}>
        <Header
          variants={container}
          initial="hidden"
          animate="show"
          className="title"
        >
          <Hide>
            <motion.h2 variants={titleAnimation}>Technology</motion.h2>
          </Hide>
          <Hide>
            <motion.h2 variants={titleAnimation}>
            Connects <span>Japan</span>
            </motion.h2>
          </Hide>
          <Hide>
            <motion.h2 variants={titleAnimation}>
             and <span>India</span>
            </motion.h2>
            <motion.h4 variants={titleAnimation}>
             <span>13th May, 2023</span> @ Faculty Hall, IISc
            </motion.h4>
          </Hide>
        </Header>

        <motion.p variants={fade}>
        The Japan Habba Tech Fair is a India-Japanese technical conference held annually in IISc Bangalore. It is colocated with the Japan Habba cultural event, and seeks to bring together an <span2>audience of enthusiastic people from academia and industry, interested in Indo-Japan collaboration.</span2> - Tickets are live!
        </motion.p>

        <Link className="non-styled-link" to={{pathname: "https://in.bookmyshow.com/events/japan-habba-tech-jobs-fair-2023/ET00356337"}} target="_blank">
          <motion.button variants={fade}>Buy Conference Tickets</motion.button>
        </Link>

      </Description>
      <ImageContainer>
        <motion.img
          variants={photoAnimation}
          initial="hidden"
          animate="show"
          src={techpic}
          alt="Professional Tech Conference"
        />
      </ImageContainer>
      <Wave />
    </Layout>
  );
};

export default AboutSection;
