import React from "react";
import styled from "styled-components";
import home1 from "../img/home1.png";
import { Link } from "react-router-dom";
import { Layout, Description, ImageContainer, Hide } from "../styles";
// Animation
import { motion } from "framer-motion";
import { titleAnimation, fade, photoAnimation } from "../animation";
import Wave from "./Wave";

const Header = styled(motion.div)`
  @media only screen and (min-width: 540px) and (max-width: 820px) {
    h2 {
      font-size: 3rem;
      
    }
  }
  
`;

const AboutSection = () => {
  // Framer-motion variant
  const container = {
    hidden: { x: 100 },
    show: {
      x: 0,
      transition: {
        duration: 0.75,
        ease: "easeOut",
        staggerChildren: 0.8,
        when: "afterChildren",
      },
    },
  };

  return (
    <Layout>
      <Description style={{ zIndex: 2 }}>
        <Header
          variants={container}
          initial="hidden"
          animate="show"
          className="title"
        >
          <Hide>
            <motion.h2 variants={titleAnimation}>The<span> biggest</span> celebration</motion.h2>
          </Hide>
          <Hide>
            <motion.h2 variants={titleAnimation}>
              of <span>Japanese Culture</span>
            </motion.h2>
          </Hide>
          <Hide>
            <motion.h2 variants={titleAnimation}>
              in <span>India!</span>
            </motion.h2>
          </Hide>
        </Header>

        <motion.p variants={fade}>
        Japan Habba is an Indian-Japanese cultural exchange event held annually in Bangalore, bringing in thousands of enthusiastic audience for more than 15 years!
        </motion.p>

        <Link class="cpopop" className="non-styled-link" to={{pathname: "https://in.bookmyshow.com/events/japan-habba-2023/ET00356325"}} target="_blank">
          <motion.button class="sayonara3" variants={fade}>Buy Donor Pass!</motion.button>
          <br></br>
        </Link>
        {/* <Link class="sayonara" className="non-styled-link" to={{pathname: "https://chat.whatsapp.com/EjTv54IoxAi7le9UofCZRE"}} target="_blank">
          <motion.button class="sayonara2" variants={fade}>Sponsor, Set up Booths or Perform</motion.button>
          <a>&nbsp;</a>
          </Link> */}
        <Link class="sayonara" className="non-styled-link" to={{pathname: "https://chat.whatsapp.com/EjTv54IoxAi7le9UofCZRE"}} target="_blank">
          <motion.button class="sayonara2" variants={fade}>Join our Whatsapp Community Group</motion.button>
        </Link>
        {/* <Link class="sayonara" className="non-styled-link" to={{pathname: "https://chat.whatsapp.com/EjTv54IoxAi7le9UofCZRE"}} target="_blank">
          <motion.button class="sayonara2" variants={fade}>Join our Whatsapp Community Group</motion.button>
        </Link> */}
      </Description>
      <ImageContainer>
        <motion.img
          variants={photoAnimation}
          initial="hidden"
          animate="show"
          src={home1}
          alt="guy with a camera"
        />
      </ImageContainer>
      <Wave />
    </Layout>
  );
};

export default AboutSection;
