import { motion } from "framer-motion";
import React from "react";
import styled from "styled-components";

export const PricingSection = styled.div`
  padding: 100px 0 160px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  background-color: #f26f3a;
  height: 90vh;
  /* Mobile devices iPhone, Pixel */
  @media only screen and (min-width: 320px) and (max-width: 539px) {
    height: 100%;
  }
  /* Kindel and iPad tablet  */
  @media only screen and (min-width: 540px) and (max-width: 820px) {
    height: 100%;
  }
`;

export const PricingWrapper = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 0 auto;
  @media screen and (max-width: 960px) {
    margin: 0 30px;
    display: flex;
    flex-direction: column;
    align-items: center;
  }
`;

export const PricingHeading = styled.h1`
  color: #1b1b1b;
  font-size: 48px;
  margin-bottom: 24px;
  /* Mobile devices iPhone, Pixel */
  @media only screen and (min-width: 320px) and (max-width: 539px) {
    font-size: 2rem;
    text-align: center;
  }
`;

export const PricingContainer = styled.div`
  display: flex;
  justify-content: center;
  align-items: center;
  /* Kindel and iPad tablet  */
  @media only screen and (min-width: 540px) and (max-width: 820px) {
    flex-wrap: wrap;
    width: 100%;
  }
  /* Mobile devices iPhone, Pixel */
  @media only screen and (min-width: 320px) and (max-width: 539px) {
    flex-wrap: wrap;
    width: 100%;
  }
`;

export const PricingCard = styled(motion.div)`
  background: #242424;
  box-shadow: 0 6px 20px rgba(56, 125, 255, 0.2);
  width: 280px;
  height: 500px;
  text-decoration: none;
  border-radius: 4px;
  &:nth-child(2) {
    margin: 24px;
  }
  &:hover {
    transform: scale(1.06);
    transition: all 0.3s ease-out;
    color: #1c2237;
  }
  /* Kindel and iPad tablet  */
  @media only screen and (min-width: 540px) and (max-width: 820px) {
    &:hover {
      transform: none;
    }
  }
  /* Mobile devices iPhone, Pixel */
  @media only screen and (min-width: 320px) and (max-width: 539px) {
    width: 90vw;
    &:hover {
      transform: none;
    }
    &:nth-child(2) {
      margin: 24px 0;
    }
  }
`;

export const PricingCardInfo = styled.div`
  display: flex;
  flex-direction: column;
  height: 500px;
  align-items: center;
  color: #fff;
`;

export const PricingCardIcon = styled.div`
  margin: 24px 0;
`;

export const PricingCardPlan = styled.h3`
  margin-bottom: 5px;
  font-size: 24px;
`;

export const PricingCardCost = styled.h4`
  font-size: 40px;
`;

export const PricingCardLength = styled.p`
  font-size: 14px;
  padding: 0.25rem;
  padding-bottom: 0.8rem;
`;

export const PricingCardFeatures = styled.ul`
  margin: 16px 0 32px;
  list-style: none;
  display: flex;
  flex-direction: column;
  align-items: center;
  color: #f7f7f7;
`;

export const PricingCardFeature = styled.li`
  margin-bottom: 10px;
`;
const SubsAndPacks = () => {
  return (
    <PricingSection>
      <PricingWrapper>
        <PricingHeading>Previous Year Websites</PricingHeading>
        <PricingContainer>
          <PricingCard>
            <PricingCardInfo>
              <PricingCardIcon></PricingCardIcon>
              <PricingCardPlan>Japan Habba 2020</PricingCardPlan>
              <PricingCardLength><i>Suibokuga the graceful stroke</i></PricingCardLength>
              <PricingCardCost>Offline</PricingCardCost>
              <PricingCardLength>9 Feb, 2020</PricingCardLength>
              <PricingCardFeatures>
                <PricingCardFeature>6,000+ Offline Visitors</PricingCardFeature>
                <PricingCardFeature>Special Performances</PricingCardFeature>
                <PricingCardFeature>Japanese Performances</PricingCardFeature>
                <PricingCardFeature>Anime and More!</PricingCardFeature>
              </PricingCardFeatures>
              <a target="_blank" href="https://japanhabba.org/2020/"><button>Visit Website</button></a>
            </PricingCardInfo>
          </PricingCard>
          
          <PricingCard>
            <PricingCardInfo>
              <PricingCardIcon></PricingCardIcon>
              <PricingCardPlan>Japan Habba 2022</PricingCardPlan>
              <PricingCardLength><i>Waza</i></PricingCardLength>
              <PricingCardCost>Online</PricingCardCost>
              <PricingCardLength>8-10 April, 2022</PricingCardLength>
              <PricingCardFeatures>
                <PricingCardFeature>12,000+ Attendees</PricingCardFeature>
                <PricingCardFeature>Completely Live steamed</PricingCardFeature>
                <PricingCardFeature>Interactive Discord events</PricingCardFeature>
                <PricingCardFeature>3 Special Events!</PricingCardFeature>
              </PricingCardFeatures>
              <a target="_blank" href="https://japanhabba.org/2022/"><button>Visit Website</button></a>
            </PricingCardInfo>
          </PricingCard>

          <PricingCard>
            <PricingCardInfo>
              <PricingCardIcon></PricingCardIcon>
              <PricingCardPlan>Japan Habba 2023</PricingCardPlan>
              <PricingCardLength><i>Phoenix</i></PricingCardLength>
              <PricingCardCost>Hybrid</PricingCardCost>
              <PricingCardLength>13-14 May, 2023</PricingCardLength>
              <PricingCardFeatures>
                <PricingCardFeature><b>Best ever yet!</b></PricingCardFeature>
                <PricingCardFeature><br></br></PricingCardFeature>
                <PricingCardFeature>Public Website under Dev</PricingCardFeature>
                <PricingCardFeature>More info coming soon ;)</PricingCardFeature>
              </PricingCardFeatures>
              <a target="_blank" href="https://portal.japanhabba.org"><button>Portal Login</button></a>
            </PricingCardInfo>
          </PricingCard>

        </PricingContainer>
      </PricingWrapper>
    </PricingSection>
  );
};

export default SubsAndPacks;
