import React from "react";
import styled from "styled-components";
import specialperf from "../img/specialperf.jpg";
import { Link } from "react-router-dom";
import { Layout, Description, ImageContainer, Hide } from "../styles";
// Animation
import { motion } from "framer-motion";
import { titleAnimation, fade, photoAnimation } from "../animation";
import Wave from "./Wave";

const Header = styled(motion.div)`
  @media only screen and (min-width: 540px) and (max-width: 820px) {
    h2 {
      font-size: 3rem;
      
    }
  }
  
`;

const SpecialSection = () => {
  // Framer-motion variant
  const container = {
    hidden: { x: 100 },
    show: {
      x: 0,
      transition: {
        duration: 0.75,
        ease: "easeOut",
        staggerChildren: 0.8,
        when: "afterChildren",
      },
    },
  };

  return (
    <Layout>
      <Description style={{ zIndex: 2 }}>
      <Header
          variants={container}
          initial="hidden"
          animate="show"
          className="title"
        >
          <Hide>
            <motion.h2 variants={titleAnimation}>Special Performer from <span>Japan!</span></motion.h2>
          </Hide>
          
        </Header>
        <motion.p variants={fade}>
        
        Japan Habba 2023 presents special performer Tomokuni Terukina, a Ryukyuan classical musician, coming to Bangalore, all the way from Okinawa, Japan. Terukina is a Sanshin artist/master from the Ryukyu classic Afuso-ryu Musical school. Sanshin is an essential musical instrument in Ryukyuan performing arts. The art of playing the Sanshin has been traditionally passed down from generation to generation. Terukina learnt the Sanshin from his father, late Choichi Terukina, a Sanshin grandmaster and who holds the title Living National Treasure of Japan. Visit Japan Habba this May, to experience this niche performance art of Okinawa by TomokuniTerukina.
        </motion.p>

        <Link class="cpopop" className="non-styled-link" to={{pathname: "https://in.bookmyshow.com/events/japan-habba-2023/ET00356325"}} target="_blank">
          <motion.button class="sayonara3" variants={fade}>Watch him perform live at Japan Habba!</motion.button>
          <br></br>
        </Link>
        {/* <Link class="sayonara" className="non-styled-link" to={{pathname: "https://chat.whatsapp.com/EjTv54IoxAi7le9UofCZRE"}} target="_blank">
          <motion.button class="sayonara2" variants={fade}>Sponsor, Set up Booths or Perform</motion.button>
          <a>&nbsp;</a>
          </Link> */}
        {/* <Link class="sayonara" className="non-styled-link" to={{pathname: "https://chat.whatsapp.com/EjTv54IoxAi7le9UofCZRE"}} target="_blank">
          <motion.button class="sayonara2" variants={fade}>Join our Whatsapp Community Group</motion.button>
        </Link> */}
        {/* <Link class="sayonara" className="non-styled-link" to={{pathname: "https://chat.whatsapp.com/EjTv54IoxAi7le9UofCZRE"}} target="_blank">
          <motion.button class="sayonara2" variants={fade}>Join our Whatsapp Community Group</motion.button>
        </Link> */}
      </Description>
      <ImageContainer>
        <motion.img
          variants={photoAnimation}
          initial="hidden"
          animate="show"
          src={specialperf}
          alt="Special Performer"
        />
      </ImageContainer>
      <Wave />
    </Layout>
  );
};

export default SpecialSection;
