import React from "react";
import { Route, Switch, useLocation } from "react-router-dom";
import GlobalStyles from "./components/GlobalStyles";
import Nav from "./components/Nav";
// Import pages down here
import AboutUs from "./pages/AboutUs";
import ContactUs from "./pages/ContactUs";
import MovieDetail from "./pages/MovieDetail";
import OurWork from "./pages/OurWork";
import TechFair from "./pages/TechFair.js";
import Pre from "./pages/Pre-Events.js";
import Sponsors2 from "./pages/Sponsors.js";
import NotFound from "./pages/NotFound";
import Sponsors from "./pages/Sponsors";

// Animation
import { AnimatePresence } from "framer-motion";
import Subscriptions from "./pages/Subscriptions";

function App() {
  const location = useLocation();

  return (
    <div>
      <GlobalStyles />
      <Nav />
      <AnimatePresence exitBeforeEnter>
        <Switch 
          location={location} 
          key={location.pathname}
        >
          <Route path="/" exact>
            <AboutUs />
            <Route path="/sponsors" exact>
              <Sponsors2 />
            </Route>
          </Route>
          <Route path="/techfair" exact>
            <TechFair />
          </Route>
          <Route path="/pre-events" exact>
            <Pre />
          </Route>
          <Route path="/contact" exact>
            <ContactUs />
          </Route>
          <Route path="/past-year-websites" exact>
            <Subscriptions />
          </Route>
          <Route component={NotFound} />
          <Route path="/work/:id" exact>
            <MovieDetail />
          </Route>
        </Switch>
      </AnimatePresence>
    </div>
  );
}

export default App;
