import React from "react";
import styled from "styled-components";
import { Layout } from "../styles";
import Toggle from "./Toggle";
import { NavLink } from "react-router-dom";
// Animation
import { AnimateSharedLayout } from "framer-motion";
import { useScroll } from "../hooks/useScroll";

const FAQ = styled(Layout)`
  display: block;
  span {
    display: block;
  }
  h2 {
    padding-bottom: 2rem;
    font-weight: lighter;
  }
  h4 {
    font-size: 1.7em;
    letter-spacing: 1.5px;
    cursor: pointer;
  }
  .faq-line {
    background-color: #ccc;
    height: 0.2rem;
    margin: 2rem 0;
    width: 100%;
  }
  .question {
    padding: 0.5rem 0rem;
    svg {
      cursor: pointer;
    }
  }
  .speca{
    display:flex;
  }
  .speca2{
    color: #f26f3a;
    display:flex;
  }
  .answer {
    padding: 2rem 0;
    p {
      padding: 1rem 0;
      font-size: 1.2rem;
      letter-spacing: 1.5px;
    }
    ul {
      list-style-type: circle;
      li {
        margin-left: 2rem;
        color: #f26f3a;
        font-size: 1.1rem;
      }
    }
  }
  .header-inline {
    display: flex;
    justify-content: space-between;
  }
  line {
    stroke-width: 10px;
    stroke-linecap: round;
    fill: transparent;
  }
  /* Mobile devices iPhone, Pixel */
  @media only screen and (min-width: 320px) and (max-width: 539px) {
    display: flex;
    flex-direction: column;
    h2 {
      font-size: 3rem;
    }
    .question, .answer {
      width: 100%;
      font-size: 0.7rem;
    }
  }
`;

const FaqSection = () => {
  const [element] = useScroll();

  return (
    <FAQ initial="hidden" ref={element} className="faq">
      <h2>
        <span>FAQ</span>
      </h2>
      <AnimateSharedLayout>

      <Toggle title="I want to come as an Audience for the event">
          <div className="answer">
            <p>Thank you! We are glad that you want to visit Japan Habba this year.</p>
            <p className="speca">
              <u>More details about entry for the event will be posted soon</u> Meanwhile, you can
              Join our Whatsapp Community Group
              to get notified first when the information is out!
            </p>
          </div>
        </Toggle>

      <Toggle title="I am really excited and interested in Japanese Culture/Anime/Japan! I want to get involved more!">
          <div className="answer">
            <p>The best way to get involved in the community is to start contributing to it</p>
            <p>
              We welcome you to apply for becoming a Volunteer or Performing at JapanHabba! Head over to
              the portal to get started.
              {/* Easiest way to communicate with us is to email us with all the
              queries that you might have. You can email us at 
              <a href="mailto:webmaster@japanhabba.org">
              webmaster@japanhabba.org
              </a>
              <NavLink
                className="styled-link"
                to={{pathname: "mailto:webmaster@japanhabba.org"}}
                style={{ width: "fit-content", color: "#f26f3a" }}
              >
              Contact Us
              </NavLink> */}
            </p>
          </div>
        </Toggle>

        <Toggle title="I have some queries!">
          <div className="answer">
            <p>Let's have some meaningful conversations</p>
            <p className="speca">
              Easiest way to communicate with us is to email us with all the
              queries that you might have. You can&nbsp;
              <NavLink
                className="styled-link"
                to={{pathname: "mailto:webmaster@japanhabba.org"}} target="_blank"
                style={{ width: "fit-content", color: "#f26f3a" }}
              >
              Contact Us
              </NavLink>&nbsp;here or also ask them out.
            </p>
          </div>
        </Toggle>
        <Toggle title="WhatsApp Community Group">
          <div className="answer">
            <p>We welcome you to join the Official Japan Habba WhatsApp Community group for news, updates and discussion! </p>
            <NavLink
                className="styled-link"
                to={{pathname: "https://chat.whatsapp.com/EjTv54IoxAi7le9UofCZRE"}} target="_blank"
                style={{ width: "fit-content", color: "#f26f3a" }}
              >
              Join the Community Group
              </NavLink>
            {/* <p>
              <b>
                <em>CONTENT THAT CUTS THROUGH</em>
              </b>
            </p>
            <p>
              Rise above the clutter with remarkable video content that engages
              your target audience, inspires action and makes an impact.
            </p>
            <p>
              <b>
                <em>REACH MORE OF YOUR TARGET AUDIENCE</em>
              </b>
            </p>
            <p>
              Content creation is just the beginning at Skeleton. We maximise
              your video content's reach throughout the buyer's journey with
              social edits, optimisation techniques and paid promotion.
            </p>
            <p>
              <b>
                <em>A STRESS-FREE PROCESS</em>
              </b>
            </p>
            <p>
              With over 15 years of experience creating remarkable vidoes for
              world-class brands, our process is simple, smooth and
              straightforward from day one. No worries.
            </p> */}
          </div>
        </Toggle>
        <Toggle title="When and where is the event being held?">
          <div className="answer">
            <p>This year, <b>Japan Habba</b> will be held on on <b>May 14, 2023</b> at <b>National 
              Science Seminar Complex (NSSC),</b> Indian Institute of Science, Bangalore.</p>

            <br></br> <p>The <b>Japan Habba Tech Fair</b> will be held at the <b>Faculty Hall,</b> Indian Institute of Science (IISc) 
            on May 13, 2023 (Saturday), the day before Japan Habba.
            </p>


          </div>
        </Toggle>

      </AnimateSharedLayout>
    </FAQ>
  );
};

export default FaqSection;
