import styled from "styled-components";
import { motion } from "framer-motion";

export const Layout = styled(motion.div)`
  min-height: 90vh;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 5rem 10rem;
  color: white;
  /* iPad Pro tablet */
  @media only screen and (min-width: 821px) and (max-width: 1200px) {
    padding: 1rem 1rem;
  }
  /* Kindel and iPad tablet  */
  @media only screen and (min-width: 540px) and (max-width: 820px) {
    padding: 3rem;
    flex-direction: column;
    justify-content: center;
  }
  /* Mobile devices iPhone, Pixel */
  @media only screen and (min-width: 320px) and (max-width: 539px) {
    padding: 1rem;
    width: 100%;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    h2{
      margin: 0 1rem 0 1rem;
      font-size: 1rem;
    }
  }
`;
export const Description = styled.div`
  flex: 1;
  padding-right: 5rem;
  h2 {
    font-weight: lighter;
    color: #f7f7f7;
  }
  .player-wrapper {
    position: relative;
    padding-top: 56.25%; /* Player ratio: 100 / (1280 / 720) */
  }
  .sayonara
  { align: center;
    padding: 20px;

    font-color: 000000;
  }
  .sayonara2
  {
    align: center;
    margin: 0;
    margin-top: 1.5rem;
    font-size: 1rem;
    font-weight: bold;
    padding: 0.5rem;
    font-color: 000000;
    
  }
button.sayonara2
{
  border: 3px solid #2F4858;
  font-size: 1.0rem;
  &:hover {
        
    background: #2F4858;
        color: white;
        font-weight: bold;
        font-size: 1.05rem;

    }
}
  button.sayonara3 {
    padding: 0.9rem;
    background-color: #f26f3a;
    
    color: #1b1b1b;
    transition: all 0.5s ease;
    font-family: 'Inter', sans-serif;
    &:hover {
        
    background: transparent;
        color: white;

    }
}

  .react-player {
    position: absolute;
    top: 0;
    left: 0;
  }
  /* Kindel and iPad tablet  */
  @media only screen and (min-width: 540px) and (max-width: 820px) {
    display: flex;
    flex-direction: column;
    flex: 0;
    align-items: center;
    padding-right: 1rem;
    p {
      padding: 2rem;
      text-align: center;
    }
  }
  /* Mobile devices iPhone, Pixel */
  @media only screen and (min-width: 320px) and (max-width: 539px) {
    padding: 0;
    h2 {
      font-size: 2rem;
      text-align: center;
    }
    button {
      margin: 0 auto;
      font-size: 0.8rem;
      padding: 10px 5px;
    }
    p {
      font-size: 1rem;
      padding-left: 20px;
      padding-right: 20px;
      text-align: center;
    }
    .sayonara2
  {
    align: center;
    margin: 0;
    margin-top: 1rem;
    margin:1rem 4rem;
    font-size: 0.7rem;
    font-weight: bold;
    padding: 0.4rem;
    
  }

  }
`;
export const ImageContainer = styled.div`
  flex: 1;
  z-index: 2;
  overflow: hidden;
  img {
    width: 100%;
    height: 45em;
    object-fit: cover;
    -webkit-user-drag: none;
    margin-top: 1rem;
  }
  /* Kindel and iPad tablet  */
  @media only screen and (min-width: 540px) and (max-width: 820px) {
    img {
      margin-top: 2rem;
      height: 30em;
      width: 35rem;
      object-fit: cover;
    }
  }
  /* Mobile devices iPhone, Pixel */
  @media only screen and (min-width: 320px) and (max-width: 539px) {
    margin-bottom: 1rem;
    img {
      margin-top: 1.6rem;
      height: 30em;
      width: 100%;
      object-fit: cover;
    }
  }
`;
export const Hide = styled.div`
  overflow: hidden;
`;
